.Broadcast {
    h3 {
        margin-bottom: .25em;
    }
    
    .yt-video {
        min-width: 0;
        display: grid;
        max-width: calc(120px * 3);
        grid-template-columns: 1fr 1fr 1fr;
        
        .thumb {
            display: block;
            max-width: 100%;
            padding-bottom: 56.25%;
            background-position: center;
        }
    }
}